:root {
  /* COLORS CSS  */
  --black-600: #181818;
  --black: #000;
  --white: #fff;
  --danger-800: #ff0000;
  --grey-900: #b2b2b2;
  --orange-800: #ff6900;
  /*=========== TYPOGRAPHY ROOTS CSS =========*/
  /*------ FONT SIZE -----*/
  --fs-sm: 16px;
  --fs-md: 18px;
  --fs-lg: 30px;
  --fs-xl: 40px;
  --fs-xxl: 48px;

  /*------- FONT WEIGHT  -----*/
  --fw-light: 300;
  --fw-regular: 400;
  --fw-medium: 500;
  --fw-semibold: 600;
}

@media (max-width: 575.98px) {
  :root {
    --fs-lg: 26px;
    --fs-xl: 34px;
    --fs-xxl: 40px;
  }
}

