/* UNDER LINE HOVER WITH AFTER  */

.club-header .nav-link:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #fff;
  background-color: var(--white);
  transition: all 0.3s ease-in-out 0s;
}

.club-header .nav-link:hover::after {
  width: 100%;
}
@media (max-width: 421px) {
  .logo img {
    height: 40px;
    width: 40px;
  }
  .logo {
    font-size: 20px !important;
  }
}
