/* LEAVES ANIMATIONS  */

.leaves-child {
  position: absolute;
  -webkit-animation: float 6s linear infinite;
          animation: float 6s linear infinite;
}

.leaves-child:nth-child(1) {
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
  left: 45%;
  top: 50%;
}

.leaves-child:nth-child(2) {
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
  left: 66%;
  top: 10%;
}

.leaves-child:nth-child(3) {
  -webkit-animation-delay: 5s;
          animation-delay: 5s;
  left: 77%;
  bottom: 36%;
}

.leaves-child:nth-child(4) {
  -webkit-animation-delay: 7s;
          animation-delay: 7s;
  right: 80%;
  bottom: 32%;
}
.leaves-child:nth-child(5) {
