}
.leaves-child:nth-child(7) {
  -webkit-animation-delay: 8s;
          animation-delay: 8s;
  right: 30%;
  bottom: 44%;
}
.leaves-child:nth-child(8) {
  -webkit-animation-delay: 6s;
          animation-delay: 6s;
  right: 50%;
  bottom: 16%;
}
.leaves-child:nth-child(9) {
  -webkit-animation-delay: 4s;
